@value makerBlack, makerWhite from '../../common/color.css';

.form {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    border-top: 1px solid makerBlack;
    border-left: 1px solid makerBlack;
    margin-bottom: 1em;
}

.input, .select, .textarea, .button, .fileInput {
    font-size: 0.8rem;
    width: 100%;
    border: 0;
    padding: 0.5em;
    border-bottom: 1px solid makerBlack;
    border-right: 1px solid makerBlack;
    background: makerWhite;
}

.input, .select {
    flex: 1 1 30%;
}

.fileInput {
    flex: 1 1 50%;
    padding: 0;
}

.input:focus, .text:focus, .select:focus, .button:focus {
    outline: 0;
}

.textarea {
    flex-basis: 100%;
}